
.intro {
  width: 100%;
  height: 100vh;
  background: url('../images/photo/bg-images_1920px.jpg') center no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative;
  &::before {
    content: ' ';
    background-color: rgba($colorbg, .85);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1; } }







