/* Размер шрифта будет увеличиваться в зависимости от размера экрана */
.header {
  padding: 10px 0;
  border-bottom: 1px solid rgba(144, 158, 158, 0.2);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  transition: all .5s; }
  .header__box {
    display: flex;
    align-items: center; }
  .header__logo {
    display: flex;
    align-items: center; }
  .header__icon {
    width: 55px;
    height: 55px;
    border: 1px solid #909e9e;
    border-radius: 50%; }
    .header__icon--img {
      width: 100%;
      border-radius: 50%;
      border: 2px solid transparent; }
  .header__descriptioon {
    padding-left: 20px; }
    .header__descriptioon--name {
      font-size: 1em;
      color: #d4d3d3;
      font-weight: bold;
      padding-bottom: 5px; }
    .header__descriptioon--profession {
      font-size: 0.775em;
      color: #669e85;
      font-weight: bold;
      font-style: italic; }
  .header__link {
    margin-left: auto;
    color: #d4d3d3;
    display: flex;
    align-items: center; }
  .header__language {
    text-transform: uppercase;
    display: inline-block;
    color: #909e9e;
    font-size: .775em; }
  .header__cocial-icon {
    display: flex;
    align-items: center; }
  .header__social--text {
    color: transparent; }
  .header__social--gh {
    margin-left: 22px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: transparent;
    position: relative;
    border: 2px solid #909e9e;
    transition-duration: 600ms;
    transition-property: background-color; }
    .header__social--gh:hover {
      background-color: #909e9e; }
      .header__social--gh:hover::before {
        color: #ebcb8d; }
    .header__social--gh::before {
      content: '\f09b';
      position: absolute;
      font-family: 'fontAwesome';
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 20px;
      color: #909e9e;
      transition-duration: 500ms;
      transition-property: color; }
  .header__social--vk {
    margin-left: 22px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: transparent;
    position: relative;
    border: 2px solid #909e9e;
    transition-duration: 600ms;
    transition-property: background-color; }
    .header__social--vk:hover {
      background-color: #909e9e; }
      .header__social--vk:hover::before {
        color: #ebcb8d; }
    .header__social--vk::before {
      content: '\f189';
      position: absolute;
      font-family: 'fontAwesome';
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 15px;
      color: #909e9e;
      transition-duration: 500ms;
      transition-property: color; }
  .header__social--ln {
    margin-left: 22px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: transparent;
    position: relative;
    border: 2px solid #909e9e;
    transition-duration: 600ms;
    transition-property: background-color; }
    .header__social--ln:hover {
      background-color: #909e9e; }
      .header__social--ln:hover::before {
        color: #ebcb8d; }
    .header__social--ln::before {
      content: '\f0e1';
      position: absolute;
      font-family: 'fontAwesome';
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 15px;
      color: #909e9e;
      transition-duration: 500ms;
      transition-property: color; }

.header__scrolled {
  padding: 3px 0;
  background: rgba(14, 52, 56, 0.98);
  transition: all 0.5s;
    transition-height: 60px; }

.intro {
  width: 100%;
  height: 100vh;
  background: url("../images/photo/bg-images_1920px.jpg") center no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative; }
  .intro::before {
    content: ' ';
    background-color: rgba(14, 52, 56, 0.85);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1; }

*,
::before,
::after {
  box-sizing: border-box; }

